import React, {useState} from 'react'

//Hide the submit button if the placeholder option is selected
function buttonHider() {
    document.querySelector('.button').style.display="block";
    const listCheck = document.querySelector('.leagueList').value;
    if (listCheck === "null") {
        document.querySelector('.button').style.display="none";
    }
}

export default function SearchTeams() {
    
    //states- input query, teams
    const [query, setQuery] = useState('');
    const [teamList, setTeams] = useState([]);
    
    const searchTeams = async (e) => {
        e.preventDefault();
        
        const url = `https://www.thesportsdb.com/api/v1/json/1/searchteams.php?l=${query}`;
        
        try {
            const response = await fetch(url);
            const data  = await response.json();
            setTeams(data.teams);
        }catch(err){
            console.error(err);
        }
    }
    return (
        <>
        <form className="form" onSubmit={searchTeams} onChange={buttonHider}>
            <div className="select">
                <select id="format" className="leagueList" name="query" value={query} onChange={(e) => setQuery(e.target.value)}>
                    <option value="null">Select League</option>
                    <option value="NFL">NFL</option>
                    <option value="NBA">NBA</option>
                    <option value="NHL">NHL</option>
                    <option value="MLB">MLB</option>
                </select> 
            </div>
            <button className="button" type="submit" style={{display: "none"}}>Submit</button>
        </form>
        <div className="card-list">
            {teamList.map(team => (
                <div className="card" key={team.idTeam}>
                    <img className="teamLogo" src={team.strTeamBadge} alt=""/>
                    <h3 className="teamName">{team.strTeam}</h3>
                    <p className="est">{"Founded: " + team.intFormedYear}</p>
                    <img className="stadiumPic" src={team.strStadiumThumb} width="185px" alt=""/>
                    {team.strStadiumThumb == null &&
                        <img className="stadiumPic" src={require('./img/null.jpg')} width="185px" alt=""/>
                    }
                    <p className="stadiumName">{team.strStadium}</p>
                    <p className="stadiumLocation">{team.strStadiumLocation}</p>
                    <div className="socials">
                        {team.strFacebook != null &&
                            <a href={"https://" + team.strFacebook} target="_blank" rel="noopener noreferrer">
                                <img src={require('./img/f.png')} alt=""/>
                            </a>
                        }
                        {team.strTwitter != null &&
                            <a href={"https://" + team.strTwitter} target="_blank" rel="noopener noreferrer">
                                <img src={require('./img/t.png')} alt=""/>
                            </a>
                        }
                        {team.strInstagram != null &&
                            <a href={"https://" + team.strInstagram} target="_blank" rel="noopener noreferrer">
                                <img src={require('./img/i.png')} alt=""/>
                            </a>
                        }
                    </div>
                </div>
            ))}
        </div>
        </>
    )
}

